.nav_first_div {
	background-color: var(--footer_black);
	padding: 3% 12%;
}

.nav_first_one {
	display: flex;
	padding: 3%;
}

.nav_first_one_one {
	width: 70%;
	display: flex;
	flex-wrap: wrap;
}

.nav_first_one_one a {
	display: block;
	text-decoration: none;
	color: gray;
	font-size: 1.2rem;
	margin-right: 6%;
	margin-bottom: 3%;
	letter-spacing: 1px;
	font-weight: lighter;
}

.nav_first_one_one a:hover {
	color: var(--red);
	cursor: pointer;
}

.nav_first_one_two {
	width: 30%;
	text-align: right;
	color: var(--milk_white);
}

.nav_first_one_two a {
	font-size: 1.4rem;
	margin-left: 12%;
	color: var(--milk_white);
	display: inline-block;

	transition: 0.3s ease-in-out;
}

.nav_first_one_two a:hover {
	/* font-size: 1.7rem; */
	transform: scale(1.25);
}

.nav_first_two {
	padding: 3%;
	border-top: 1px solid var(--gray);
	display: flex;
}

.nav_first_two_one img {
	width: 15%;
	height: auto;
	margin-right: 9%;
}

.nav_first_two_one {
	margin-left: 6%;
	width: 70%;
}

.nav_first_two_two {
	width: 30%;
}

.nav_first_two_two img {
	width: 100%;
	height: auto;
}

.nav_second_div {
	background-color: var(--footer_white);
	padding: 3% 12%;
	display: flex;
}

.nav_second_one {
	width: 5%;
}

.nav_second_one img {
	width: 100%;
	height: auto;
}

.nav_second_two {
	width: 65%;
	padding-left: 3%;
}

.nav_second_two a {
	text-decoration: none;
	color: var(--gray);
	font-size: 1.5rem;

	margin-right: 5%;
}

.nav_second_two p {
	font-size: 1.3rem;
	font-weight: lighter;
	color: var(--gray);
	margin: 0;
}

.nav_second_two_two h3 {
	font-size: 1.25rem;
	font-weight: lighter;
	color: var(--gray);
	letter-spacing: 1px;
}

.nav_second_two_two {
	margin-top: 8%;
}

.nav_second_three {
	width: 30%;
}

.nav_second_three button {
	padding: 4% 6%;
	border: 2px solid var(--gray);
	outline: 2px solid var(--footer_white);
	color: var(--gray);
	font-size: 1.1rem;
	transition: 0.3s ease-in-out;
}

.nav_second_three button:hover {
	border: 4px solid var(--footer_white);
	outline: 2px solid var(--gray);
	cursor: pointer;
}
